import { createTheme } from '@material-ui/core/styles'

// assets
import colors from 'assets/scss/_themes-vars.module.scss'

// project imports
import componentStyleOverrides from './compStyleOverride'
import themePalette from './palette'
import themeTypography from './typography'

/**
 * Represent theme style and structure as per Material-UI
 * @param {JsonObject} customization customization parameter object
 */
export function theme(customization) {
  const color = colors
  const mode = customization.navType

  const themeOption = {
    colors: color,
    heading: mode === 'light' ? color.grey900 : color.darkTextTitle,
    paper: mode === 'light' ? color.paper : color.darkPaper,
    backgroundDefault: mode === 'light' ? color.paper : color.darkBackground,
    background: mode === 'light' ? color.primaryLight : color.darkPrimaryLight,
    darkTextPrimary: mode === 'light' ? color.grey700 : color.darkTextPrimary,
    darkTextSecondary:
      mode === 'light' ? color.grey500 : color.darkTextSecondary,
    textDark: mode === 'light' ? color.grey900 : color.grey500,
    menuSelected:
      mode === 'light' ? color.secondaryDark : color.darkSecondaryDark,
    menuSelectedBack:
      mode === 'light' ? color.secondaryLight : color.darkSecondaryLight,
    divider: color.grey200,
    customization,
  }

  return createTheme({
    direction: 'ltr',
    palette: themePalette(themeOption),
    mixins: {
      toolbar: {
        minHeight: '48px',
        padding: '16px',
        '@media (min-width: 600px)': {
          minHeight: '48px',
        },
      },
    },
    breakpoints: {
      values: {
        xs: 0,
        sm: 600,
        md: 960,
        lg: 1280,
        xl: 1920,
      },
    },
    typography: themeTypography(themeOption),
    components: componentStyleOverrides(themeOption),
  })
}

export default theme
